<template>
    <div class="table-wrap">
        <h1>テーブルレイアウト設定</h1>
        <div class="button-container">
            <button class="add-button" @click="add">追加する</button>
        </div>
        <div class="grid">
            <table id="tableLayout">
                <thead>
                    <tr>
                        <th>テーブル名</th>
                        <th>最大収容人数</th>
                        <th>禁煙・喫煙</th>
                        <th>席タイプ</th>
                        <th>表示</th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody v-if="tables.length > 0">
                    <tr v-for="(table, value) in tables" :key="value" :id="value">
                        <td class="table-name">{{ table.name }}</td>
                        <td class="table-capacity">{{ table.low_capacity }}名〜{{ table.capacity }}名</td>
                        <td class="table-smoking">{{ table.smoking }}</td>
                        <td class="table-type">{{ table.type }}</td>
                        <td>
                            <button class="display-btn" v-if="table.display" @click="toggleDisplay(table)">表示</button>
                            <button class="display-btn hide" v-else @click="toggleDisplay(table)">非表示</button>
                        </td>
                        <td><i class="fa-lg fa-solid fa-pen-to-square edit-button" @click="edit(table)"></i></td>
                        <td><i class="fa-lg fa-solid fa-trash delete-button" @click="removeItem(table)"></i></td>
                    </tr>
                </tbody>
            </table>
        </div>
    </div>
</template>

<script>
import Ajax from '@/lib/Ajax';

export default {
    name:'TableLayout',
    data() {
        return {
            tables: [],
            keyVisible: false
        }
    },
    mounted() {
        this.getTables();
        this.$emit('setDisplay', 'table');
    },
    emits:[
        'setDisplay',
        'openPop',
        'setData'
    ],
    methods: {
        getTables() {
            Ajax('/api/master/tables')
            .then(res => res.json())
            .then(dat => {
                this.tables = dat;
            });
        },
        /**
         * 
         * PUT
         * @param {*} e 
         */
        toggleDisplay(item) {
            Ajax(`/api/master/tables/${item.id}`, 'PUT', item);
            setTimeout(this.getTables, 100);
        },

        /**
         * 
         * DELETE
         * @param {*} tableData
         */
        removeItem(tableData) {
            Ajax(`/api/master/tables/${tableData.id}`, 'DELETE');
            setTimeout(this.getTables, 100);
        },

        edit(item) {
            this.$emit('setData', item);
            this.$emit('openPop');
        },
        
        add() {
            this.$emit('setData', null);
            this.$emit('openPop');
        }
    }
}
</script>

<style>
#tableLayout .table-name {
    background: none;
    font-size: 2rem;
    border: solid 1px #000;
}
</style>
